.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.logo-bar {
  background-image: url('./Image/FondoLogoBar.jpeg'); /* Ruta de la imagen */
  background-size: cover; /* Ajusta la imagen al tamaño del contenedor */
  background-position: center; /* Centra la imagen */
  width: 100%; /* Ancho completo */
  height: 300px; /* Altura del contenedor */
  display: flex; /* Flexbox para centrar contenido */
  justify-content: center; /* Centrado horizontal */
  align-items: center; /* Centrado vertical */
}


.logo-intro {
  display: flex; /* Usar flexbox para alinear elementos */
  justify-content: center; /* Centrar horizontalmente */
  align-items: center; /* Centrar verticalmente */
  position: fixed; /* Fija el contenedor a la pantalla */
  top: 0; /* Asegura que esté en la parte superior */
  left: 0; /* Asegura que esté en el lado izquierdo */
  width: 100vw; /* 100% del ancho de la pantalla */
  height: 100vh; /* 100% del alto de la pantalla */
  background-color: #f5f5f5; /* Fondo claro de ejemplo */
  overflow: hidden; /* Deshabilitar el desplazamiento */
  box-sizing: border-box; /* Asegura que padding y borde no aumenten el tamaño */
  touch-action: none; /* Previene el movimiento en dispositivos táctiles */
}



.logo-intro img, .logo-intro div {
  max-width: 90%; /* Ajustar tamaño máximo al 90% del ancho */
  max-height: 90%; /* Ajustar tamaño máximo al 90% del alto */
}

/* Estilos responsivos si necesitas agregar ajustes */
@media (max-width: 768px) {
  .logo-intro img, .logo-intro div {
    max-width: 80%; /* Reducir el tamaño en pantallas más pequeñas */
    max-height: 80%;
  }
}

@media (max-width: 480px) {
  .logo-intro img, .logo-intro div {
    max-width: 70%; /* Reducir aún más el tamaño para pantallas pequeñas */
    max-height: 70%;
  }
}


.navbar .navbar-nav .nav-link {
  font-size: 1.35rem; /* Ajusta el tamaño según sea necesario */
}

.navbar-brand {
  font-size: 1.5rem; /* Cambia el tamaño de la fuente para el logo */
}

.fondo-nosotros {
  background-image: url('./Image/Nosotros1.jpg'); /* Ruta a tu imagen de fondo */
  background-size: cover;  /* Hace que la imagen cubra todo el fondo */
  background-position: center; /* Centra la imagen */
  width: 100%; /* Asegura que el fondo ocupe todo el ancho */
  height: 100vh; /* Asegura que el fondo ocupe toda la altura de la pantalla */
}

.fondo-nosotros p {
  text-align: center; /* Centra el texto */
  font-size: 1.5rem; /* Aumenta el tamaño de la fuente */
  font-family: 'Dancing Script', cursive; /* Aplica la fuente script */
  color: white; /* Color de texto si lo deseas */
  line-height: 1.6; /* Ajusta el interlineado para mayor legibilidad */
}


